.charter-title {
  font-size: 35px;
  font-weight: 200;
  margin-top: 40px;
  margin-bottom:50px;
  font-weight: bold;
}

.title-section{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.charter-subtitle{
  font-size: 30px;
  font-weight: bold;
}

.charter-intro-subtitle {
  font-size: 30px;
}

.charter-intro-subtitle {
  margin: 30px 0px;
}

.charter-desc {
  font-size: 20px;
  margin-bottom: 50px;
}

.charter-condition {
  font-size: 15px;
}

.charter-header {
  text-align: center;
}

.charter-desc {
  text-align: left;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.charter-desc p {
  padding: 5px 0px;
}

.charter-partnership {
  display: flex; 
  flex-direction: column;
  justify-content: center; 
  align-items: center; 
  text-align: left; 
  margin: 20px auto; 
  background-color: #DEE3D2;
  width: 60%;
  padding: 30px 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); 
  border-radius: 10px;
}

.charter-partnership ul {
  text-align: left; 
  font-size: 20px;
  margin-top: 20px;
}

.charter-partnership li {
  list-style-position: inside;
  margin-bottom:5px;
}

.charter-prices {
  text-align: center;
  margin: 10px 0px;
}

.charter-costs-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  width: 80%; 
  margin: 20px auto; 
}

.charter-costs-box {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  padding: 10px; 
  text-align: center; 
  background: linear-gradient(rgba(253, 253, 253, 0.4), rgba(253, 253, 253, 0.4)), url('./img/card-front-texture.jpg');
  background-size: cover;
  font-size: 20px;
}

.charter-costs-box-price {
  display: flex;
  border: 1px solid #ccc;
  padding: 10px; 
  text-align: center; 
  background: linear-gradient(rgba(253, 253, 253, 0.4), rgba(253, 253, 253, 0.4)), url('./img/card-front-texture.jpg');
  background-size: cover;
  font-size: 30px;
  justify-content: center;
  align-items: center;
}

.charter-sub-title {
  font-size: 25px;
}

.charter-funds {
  width: 60%;
  display: block;
  margin: 30px auto;
  border: solid #8A746B 2px;
  padding: 30px 30px;
  border-radius: 10px;
  background: linear-gradient(rgba(253, 253, 253, 0.6), rgba(253, 253, 253, 0.6)), url('./img/card-front-texture.jpg');
  background-size: cover;
}

.fund-steps {
  border: solid #8A746B 2px;
  padding: 20px 20px;
  margin-top: 10px;
  background-color: #DEE3D2;
}

.fund-steps p{
  font-size: 20px;
  margin-bottom:5px;
}

.fund-steps ul {
  margin-left: 20px;
}

.fund-subtitle {
  font-weight:bold;
}

.charter-policies {
  background-color: #DEE3D2;
  padding: 20px 20px;
  width: 70%;
  margin: 20px auto;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); 
  border-radius: 10px;
}

.charter-policies ul{
  margin-top:20px;
  margin-left: 20px;
  font-size: 20px;
}

.charter-policies li{
  margin-bottom:10px;
}

.pict-image {
  height: 200px;
  width: auto;
}
